<template>
    <form @submit.prevent class="form-wrapper">
        <h2>Предложите свой пост</h2>
        <input v-model="post.title" type="text" placeholder="Тема поста">
        <input v-model="post.body" type="text" placeholder="Текст поста">
        <select v-model="post.img" class="select-form">
            <option value="">Важно</option>
            <option value="">Тема для размышлений</option>
            <option value="">Хорошее настроение</option>
        </select>
        <button @click="createPost"> Отправить на модерацию</button>
    </form>
</template>
<script>
export default {
      data() {
        return {
            post: {
                title: '',
                body: '',
            }
        }
    },
    methods: {
        createPost() {
            this.post.id = Date.now()
            this.$emit('create', this.post)
            this.post = {
                title: '',
                body: '',
            }
        },


    }
    
}
</script>
<style lang="scss">
    .form-wrapper {
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        padding: 20px;
        border: 2px solid black;
        border-radius: 15px;
        background-color: white;

        input,
        button {
            height: 50px;
            margin-bottom: 10px;
        }

        button {
            background-color: #324973;
            color: white;
            border: none;
            cursor: pointer;
        }

        select {
            height: 25px;
            margin-bottom: 10px;
        }
    }
</style>