<template>
    <form @submit.prevent class="form">
        <h2>Добавить задачу</h2>
        <div class="form__input">
            <input v-model="task.title"  placeholder="Заголовок" type="text">
            <input v-model="task.description"   placeholder="Описание" type="text">
        </div>
        <button @click="createTask">Добавить</button>
    </form>
</template>

<script>

export default {
    data() {
        return {
            task: {
                title: '',
                description: ''
            }
        }
    },
    methods: {
        createTask() {
            this.task.id = Date.now()
            this.task.status = false
            this.$emit('create', this.task)
            this.task = {
                title: '',
                description: ''
            }
        },


    }
    
}

</script>

<style lang="scss" scoped>
.form {
    margin: 0 auto;
    border: 2px solid black;
    border-radius: 15px;
    background-color: white;

    text-align: center;
    h2 {
        margin-bottom: 60px;
    }



    width: 600px;

    margin-bottom: 40px;
    padding: 40px;

    .form__input {
        display: flex;
        justify-content: space-around;
        margin-bottom: 30px;
    }

    button {
        background-color: #324973;
        color: white;
        border: none;
        cursor: pointer;
    }

    input,
    button {
        padding: 10px;
        margin: 5px 0;
        width: 250px;

        border-radius: 15px;

    }
}

</style>