<template>
    <main>
      <h1 class="title">ToDo-list <br> Универсальный список задач</h1>
      <p class="title">Список задач (Todo-list), написанный на стеке Vue3 + Composition Api + MockApi</p>
      <button @click="showDialog" class="dialog-button">Добавить задачу</button>
      <my-dialog  v-model:show="dialogVisible">
          <task-input  @create='addTask'/>
      </my-dialog>
      <task-list
      @delete='deleteTask'
      @complete='completeTask'
      :tasks="tasks"
      />
    </main>
</template>

<script>
import axios from 'axios'
import TaskInput from '@/components/ToDoList/TaskInput.vue'
import {getTasks} from '@/hooks/getTasks.js'
import TaskList from '@/components/ToDoList/TaskList.vue'
import MyDialog from '@/components/MyDialog.vue'
export default {
  data() {
    return {
        dialogVisible: false
    }
  },
  components: {
    TaskInput,
    TaskList,
    MyDialog
  },
    methods: {
    addTask(task) {
      this.tasks.push(task)
      this.dialogVisible = false
      return axios.post('https://633c234774afaef16402bb1a.mockapi.io/tasks', task)
    
    },

    deleteTask(item) {
      axios.delete(`https://633c234774afaef16402bb1a.mockapi.io/tasks/${item.id}`)
      this.tasks = this.tasks.filter(t => t.id !== item.id)
    },

    completeTask(item) {
      axios.put(`https://633c234774afaef16402bb1a.mockapi.io/tasks/${item.id}`, 'status=true')
      return item.status = true
    },
    showDialog() {
      this.dialogVisible = true
    },
  },
  setup(props) {
    const {tasks, isTaskLoading} = getTasks()

    return {
      tasks,
      isTaskLoading
    }
  }
}
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

main {
  display: flex;
  flex-direction: column;
}

.title {
  text-align: center;
  margin-bottom: 40px;
}

.dialog-button {
  padding: 20px;
  margin: 30px auto;
  width: 250px;

  border-radius: 15px;

  background-color: #324973;
  color: white;

  border: none;

  cursor: pointer;

}
</style>