<template>
    <div class="task-card">
        <div>
            <h2>{{model.title}}</h2>
            <p v-bind:class="{completed: model.status}">{{model.description}}</p>
        </div>
        <div>
            <button v-if="!model.status" @click="completeTask" class="task-card__button">✓</button>
            <button @click="deleteTask" v-else class="task-card__button">x</button>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        model: {
            required: true,
        }
    },
    methods: {
        deleteTask() {
            this.$emit('delete', this.model)
        },
        completeTask() {
            this.$emit('complete', this.model)
        }
    },
    setup() {
    
    }
}
</script>

<style scoped lang="scss">
.task-card {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 20px 50px 20px 20px;

    // border-radius: 15px;
    border: 2px solid black;

    margin: 0px 10px 20px 10px;


    h2 {
        margin-bottom: 20px;

    }
}
.task-card__button {
    padding: 10px;
    margin: 5px;
    height: 40px;
    width: 40px;

    border: none;

    cursor: pointer;

}

.completed {
    text-decoration: line-through;   
}


</style>