<template>
    <div>
        <ul v-if="tasks.length > 0" class="my-list">
          <transition-group name="task-list">
            <li v-for="item in tasks" :key="item.id">
              <task-card @delete='deleteTask' @complete="completeTask" :model='item'/>
            </li>
          </transition-group>
        </ul>
        <h2 v-else>В данный момент задач не создано. Создайте новую задачу, чтобы увидеть её в списке</h2>
    </div>
</template>
<script>
import TaskCard from '@/components/ToDoList/TaskCard.vue'
export default {
  methods: {
    deleteTask(model) {
      this.$emit('delete', model)
    },
    completeTask(model) {
      this.$emit('complete', model)
    }
  },
  components: {
        TaskCard
  },
  props: {
      tasks: {
          required: true
      }
  }
}
</script>
<style lang="scss" scoped>
.my-list {
  list-style: none;
}

.task-list-item {
  display: inline-block;
  margin-right: 10px;
}
.task-list-enter-active,
.task-list-leave-active {
  transition: all 0.4s ease;
}
.task-list-enter-from,
.task-list-leave-to {
  opacity: 0;
  transform: translateX(130px);
}

.task-list-move {
  transition: transform 0.4s ease;
}
</style>