<template>
    <main>
        <div class="posts-header">
            <div class="posts-greeting">
                <h1>Ваши посты</h1>
                <button @click="showDialog">Предложить свой пост</button>
            </div>
            <div class="posts-settings">
                <my-select :options='sortOptions' v-model="selectedSort" />
                <input v-model="searchQuery" placeholder="Поиск...">
            </div>
        </div>
        <my-dialog v-model:show="dialogVisible">
            <posts-form @create='addPost'/>
        </my-dialog>
        <posts-list :posts='sortedAndSearchedPosts' v-if="!isPostsLoading"/>
        <div v-else>Идет загрузка...</div>
    </main>

</template>

<script>
import axios from 'axios'
import {getPosts} from '@/hooks/getPosts.js'
import useSortedPosts from "@/hooks/useSortedPosts";
import useSortedAndSearchedPosts from "@/hooks/useSortedAndSearchedPosts";
import PostsList from '@/components/Posts/PostsList.vue'
import PostsForm from '@/components/Posts/PostsForm.vue'
import MyDialog from '@/components/MyDialog.vue'
import MySelect from '@/components/MySelect.vue'
export default {
    data() {
        return {
            dialogVisible: false,
            sortOptions: [
                {value: 'title', name: 'По названию'},
                {value: 'body', name: 'По содержимому'},
                {value: 'prefix', name: 'По префиксу'}

            ]
        }
    },
    components: {
        PostsList,
        PostsForm,
        MyDialog,
        MySelect,
    },
    methods: {
        addPost(post) {
            this.dialogVisible = false
            return axios.post('https://633c234774afaef16402bb1a.mockapi.io/suggested-post', post)
        },
        showDialog() {
            this.dialogVisible = true
        },
    },
    setup() {
        const {posts, isPostsLoading, totalPages} = getPosts(10);
        const {sortedPosts, selectedSort} = useSortedPosts(posts);
        const {searchQuery, sortedAndSearchedPosts} = useSortedAndSearchedPosts(sortedPosts)
        return {
            posts,
            totalPages,
            isPostsLoading,
            sortedPosts,
            selectedSort,
            searchQuery,
            sortedAndSearchedPosts,
        }
    }
}
</script>

<style lang="scss">
.page__wrapper {
    display: flex;
    margin-top: 15px;

}

.page {
    border: 1px solid black;
    padding: 10px;
    margin-right: 10px;
}

.current-page {
    border: 2px solid teal;
    background-color: gray;
}

.posts-header {
    margin-bottom: 80px;

    display: flex;
    justify-content: space-around;
    align-items: center;
}

.posts-greeting {
    text-align: center;
        button {
        padding: 20px;
        margin: 10px auto;
        width: 250px;


        background-color: #324973;
        color: white;

        border: none;

        cursor: pointer;
        font-size: 19px;
    }
}

.posts-settings {
    display: flex;
    flex-direction: column;

    input {
        height: 50px;
        margin-top: 10px;
        padding: 10px;
    }

}
</style>